import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Input, Table, Button } from 'antd';
import { BtnAddNew } from 'components/common/button';
import { socialObject, listSocialObject } from 'dump/data';
import SearchBar from './SearchBar';
import './style.scss'

const table1 = [
  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Giới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Chuyên nghành',
    dataIndex: 'cn',
    key: 'cn',
  },
  {
    title: 'Tên khoa',
    dataIndex: 'tenKhoa',
    key: 'tenKhoa',
  },
  {
    title: 'Tên hệ ',
    dataIndex: 'he',
    key: 'he',
  },
];

const table2 = [

  {
    title: 'Mã SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Họ tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Gới tính',
    dataIndex: 'sex',
    key: 'sex',
  },
  {
    title: 'Tên lớp',
    dataIndex: 'className',
    key: 'className',
  },
  {
    title: 'Tên đối tượng trợ cấp',
    dataIndex: 'nametc',
    key: 'nametc',
  },
  {
    title: 'Số tiền',
    dataIndex: 'sotien',
    key: 'sotien',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    key: 'note',
  },
  {
    title: 'Thao tác',
    dataIndex: 'thaotac',
    render: () => (
      <Fragment>
        <Button type="danger">Xóa</Button>
      </Fragment>
    ),
  },
];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: record.name === 'Disabled User', // Column configuration not to be checked
    name: record.name,
  }),
};

@Form.create({ name: 'formSocialObject' })
class ListPolicyObject extends Component {
  state = {};

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Fragment>
        <Card title="Chọn đối tượng trợ cấp">
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table rowSelection={rowSelection} bordered dataSource={socialObject} columns={table1} />
            </Col>
          </Row>
        </Card>
        <Card title="Danh sách đối tượng trợ cấp">
          <Form style={{ textAlign: 'right' }} onSubmit={this.handleSubmit} layout="inline">
            <Form.Item label='Chọn số tiền' >
              {getFieldDecorator(`objectSubsidy`, {
              })(
                <Input />
              )}
            </Form.Item>
            <Form.Item> <BtnAddNew title="Thêm" htmlType="submit" /></Form.Item>
          </Form>
          <Row>
            <Col span={24}>
              <Table rowSelection={rowSelection} bordered dataSource={listSocialObject} columns={table2} />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObject />
  </Page>
);



